import React, { useState } from 'react';
import { Container, Row, Col, Button } from 'react-bootstrap';
import { motion } from 'framer-motion';


import AddMetricsModal from './AddMetricsModal';
import Pilot1 from '../../assets/images/boy-pilot.png';
import Pilot2 from '../../assets/images/boy-pilot-first-officer.png';
import laptop from '../../assets/images/laptop-customer-revenue-deep-dive.png';

import './executiveCockpit.scss';

const animateContent = {
  hidden: {
    opacity: 0,
    x: '-100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animateMonitor = {
  hidden: {
    opacity: 0,
    x: '100px',
  },
  visible: {
    opacity: 1,
    x: '0',
    transition: { duration: 1 }
  }
}

const animatePerson = {
  hidden: {
    opacity: 0,
    y: '50px',
  },
  visible: {
    opacity: 1,
    y: '0',
    transition: { duration: .5, delay: .6 }
  }
}

const Metrics = ({ data }) => {
  const [modalShow, setModalShow] = useState(false);
  const [modalData, setModalData] = useState(null);
  const onCancelModal = () => setModalShow(false);
  return (
    <>

      <div className={`page-content`}>
        <Container className="container-metrics-page" fluid >
          <Row>
            <Col xl={7}>
              <div className="inner-wrap">
                <div className="block content-block">
                  <motion.div
                    variants={animateContent}
                    initial='hidden'
                    animate='visible'
                  >
                    <h2 className="title">
                      Essential analytics for agile leadership</h2>
                    <p className="text">
                      Navigate complex decision-making with a single view of all essential and up-to-date metrics,
                          leading to more timely and relevant insights.</p>
                    <Button className="btn-metrics"
                      onClick={() => {
                        setModalShow(true);
                      }}>Add Metrics</Button>
                  </motion.div>
                </div>
              </div>
            </Col>
            <Col xl={5}>
              <div className="inner-wrap">
                <div className="block image-block">
                  <motion.img
                    variants={animateMonitor}
                    initial='hidden'
                    animate='visible'
                    className="img-fluid"
                    src={laptop}
                    alt="sample flag" />
                  <motion.div
                    variants={animatePerson}
                    initial='hidden'
                    animate='visible'
                    className="person person-1" >
                    <img src={Pilot1} />
                  </motion.div>
                  <motion.div variants={animatePerson}
                    initial='hidden'
                    animate='visible'
                    className="person person-2">
                    <img src={Pilot2} />
                  </motion.div>
                </div>
              </div>
            </Col>
          </Row>
        </Container>
        <AddMetricsModal
          onCancelModal={onCancelModal}
          showBuilderModal={modalShow}
          data={modalData}
          setModalShow={setModalShow}
        />
      </div>

    </>
  )
};

export default Metrics;


